import './custom.scss';

$(() => {
	let base = "https://learndottogether.herokuapp.com"
	let urls = [
		"/api/members/count",
		"/api/posts/count",
		"/api/media/count",
		"/api/media-group/count",
	]
	let loading = 0
	let count = []
	urls.forEach(url => {
		$.get(base + url, (response) => {
			loading++
			count[url] = response;
		});
	});
	let inter = setInterval((param = loading) => {
		if (param === 4) {
			$("#members-count").html(count[urls[0]])
			$("#posts-count").html(count[urls[1]])
			$("#media-count").html(count[urls[2]])
			$("#media-group-count").html(count[urls[3]])
			clearInterval(inter);
		}
	}, 30);
})